// src/utils/formatDate.js

/**
 *  ISO-   1  2025 -.
 * @param {string} isoString 
 * @returns {string}
 */
export function formatRUDate(isoString) {
  if (!isoString) return '';
  const dt = new Date(isoString);
  return dt.toLocaleDateString('ru-RU', {
    year: 'numeric',
    month: 'long',
    day: 'numeric',
  });
}

/**
 *  ISO-    dd.mm.yy.
 * @param {string} isoString 
 * @returns {string}
 */
export function formatShortDate(isoString) {
  if (!isoString) return '';
  const dt = new Date(isoString);
  return dt.toLocaleDateString('ru-RU', {
    year: '2-digit',
    month: '2-digit',
    day: '2-digit',
  });
}
